	module.exports = "<pre><code class=\"hljs html\"><span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>v-dialog:toggle</span>=<span class=hljs-string>\"dialog\"</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"button\"</span>&gt;</span>Open dialog<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>wv-dialog</span>\n    <span class=hljs-attr>class</span>=<span class=hljs-string>\"modal\"</span>\n    <span class=hljs-attr>ref</span>=<span class=hljs-string>\"dialog\"</span>\n    <span class=hljs-attr>:width</span>=<span class=hljs-string>\"600\"</span>\n    @<span class=hljs-attr>closed</span>=<span class=hljs-string>\"onClosed\"</span>\n    <span class=hljs-attr>aria-labelledby</span>=<span class=hljs-string>\"title\"</span>\n    <span class=hljs-attr>aria-describedby</span>=<span class=hljs-string>\"description\"</span>\n  &gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>h1</span> <span class=hljs-attr>id</span>=<span class=hljs-string>\"title\"</span>&gt;</span>Hello<span class=hljs-tag>&lt;/<span class=hljs-name>h1</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>p</span> <span class=hljs-attr>id</span>=<span class=hljs-string>\"description\"</span>&gt;</span>...<span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>v-dialog:toggle</span>=<span class=hljs-string>\"dialog\"</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"button\"</span>&gt;</span>Close dialog<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n    <span class=hljs-comment>&lt;!-- lorem content --&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>v-dialog:toggle</span>=<span class=hljs-string>\"dialog\"</span> <span class=hljs-attr>type</span>=<span class=hljs-string>\"button\"</span>&gt;</span>Close dialog<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>wv-dialog</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n</code></pre> <pre><code class=\"hljs js\"><span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> {\n  <span class=hljs-comment>// ...</span>\n  methods: {\n    onClosed () {\n      <span class=hljs-built_in>window</span>.alert(<span class=hljs-string>'Dialog closed'</span>)\n    }\n  }\n}\n</code></pre> <pre><code class=\"hljs scss\"><span class=hljs-selector-class>.modal</span> {\n  <span class=hljs-attribute>background-color</span>: <span class=hljs-number>#400255</span>;\n  <span class=hljs-attribute>color</span>: <span class=hljs-number>#ffffff</span>;\n  <span class=hljs-attribute>padding</span>: <span class=hljs-number>2rem</span>;\n  <span class=hljs-attribute>text-align</span>: center;\n  <span class=hljs-attribute>border-radius</span>: <span class=hljs-number>5px</span>;\n}\n</code></pre> ";

